
.popover {
  width: auto;
  background-color: white;
  color: #97999b;
  text-align: center;
  display: inline-block;
  border-radius: 3px;
  position: absolute;
  font-size: 0.83em;
  font-weight: normal;
  border: 1px rgb(0, 0, 0) solid;
  z-index: 200000;
  max-width: 276px;
  text-align: start;
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, .2);
  border-radius: .3rem;
  opacity: 0;
  -webkit-transition: opacity 0.3s, visibility 0.3s;
  transition: opacity 0.3s, visibility 0.3s;
  visibility: hidden;
}
.show .popover {
  opacity: 1;
  visibility: visible;
}
.popover-body {
  color: #6c6e71;
}
.popover .popover_arrow {
  width: 0;
  height: 0;
  border-style: solid;
  position: absolute;
  margin: 5px;
  color: transparent;
}
.popover[x-placement^="top"] {
  margin-bottom: 18px;
}
.popover[x-placement^="top"] .popover_arrow {
  border-width: 15px 15px 0 15px;
  border-color: #d6d6d6 transparent transparent transparent;
  bottom: -15px;
  margin-bottom: 0;
}
.popover[x-placement^="top"] .popover_arrow::before {
  content: "";
  display: inline-block;
  position: absolute;
  left: -15px;
  bottom: 1.5px;
  border: solid;
  border-width: 15px 15px 0 15px;
  border-color: white transparent transparent transparent;
}
.popover[x-placement^="bottom"] {
  margin-top: 18px;
}
.popover[x-placement^="bottom"] .popover_arrow {
  border-width: 0 15px 15px 15px;
  border-color: transparent transparent #d6d6d6 transparent;
  top: -15px;
  margin-top: 0;
}
.popover[x-placement^="bottom"] .popover_arrow::before {
  content: "";
  display: inline-block;
  position: absolute;
  left: -15px;
  top: 1.45px;
  border: solid;
  border-width: 0 15px 15px 15px;
  border-color: transparent transparent white transparent;
}
.popover[x-placement^="right"] {
  margin-left: 18px;
}
.popover[x-placement^="right"] .popover_arrow {
  border-width: 15px 15px 15px 0;
  border-color: transparent #d6d6d6 transparent transparent;
  left: -15px;
  margin-left: 0;
}
.popover[x-placement^="right"] .popover_arrow::before {
  content: "";
  display: inline-block;
  position: absolute;
  top: -15px;
  left: 1.45px;
  border: solid;
  border-width: 15px 15px 15px 0;
  border-color: transparent white transparent transparent;
}
.popover[x-placement^="left"] {
  margin-right: 18px;
}
.popover[x-placement^="left"] .popover_arrow {
  border-width: 15px 0 15px 15px;
  border-color: transparent transparent transparent #d6d6d6;
  right: -15px;
  margin-right: 0;
}
.popover[x-placement^="left"] .popover_arrow::before {
  content: "";
  display: inline-block;
  position: absolute;
  top: -15px;
  right: 1.45px;
  border: solid;
  border-width: 15px 0 15px 15px;
  border-color: transparent transparent transparent white;
}



